.c-hamburger-btn {
  height: 3.5rem;
  text-align: center;
  transition: all 1.2s;
  cursor: pointer;
  display: flex;
}
.c-hamburger-btn__label {
  color: #eae0d4;
  padding: 0.6rem 0 0 1rem;
}
.c-hamburger-btn--open {
  position: relative;
  top: -7px;
  transform: translateY(2.5rem);
  width: 2.5rem;
}
.c-hamburger-btn--open, .c-hamburger-btn--open::before, .c-hamburger-btn--open::after {
  height: 1px;
  background-color: #eae0d4;
  display: inline-block;
  transition: all 0.3s;
}
.c-hamburger-btn--open::before, .c-hamburger-btn--open::after {
  content: "";
  position: absolute;
  left: 0;
}
.c-hamburger-btn--open::before {
  top: -0.6rem;
  width: 2rem;
}
.c-hamburger-btn--open::after {
  top: 0.6rem;
  width: 2rem;
}

.c-contact-btn {
  width: 2.5rem;
  height: 4rem;
  cursor: pointer;
  display: flex;
  align-items: center;
}
@media screen and (min-width: 62.5em) {
  .c-contact-btn {
    width: 0;
  }
}

.c-close-btn {
  position: relative;
  width: 0;
}
.c-close-btn, .c-close-btn::before, .c-close-btn::after {
  height: 1px;
  background-color: white;
  display: inline-block;
  transition: all 0.3s;
}
.c-close-btn--black, .c-close-btn--black::before, .c-close-btn--black::after {
  background-color: #212121;
}
.c-close-btn::before, .c-close-btn::after {
  content: "";
  position: absolute;
  left: 0;
}
.c-close-btn::before {
  transform: rotate(135deg);
  top: 1rem;
  width: 3rem;
}
.c-close-btn::after {
  transform: rotate(-135deg);
  top: 1rem;
  width: 3rem;
}

